<template>
    <el-drawer :visible.sync="open_" :direction="direction" :with-header="withHeader" :size="size"
               class="api-drawer detail" :routerId="routerId" :title="title">
        <p class="api-drawer_tit">
            <span v-html="title"></span>
            <i class="el-icon-close fr" @click="cancelDetail"></i>
        </p>
        <div class="part-info api-detail-info">
            <h3></h3>
            <div>
                <p>
                    <span class="tit"></span>
                    <span class="cont">{{ router.project_name }}</span>
                </p>
                <p>
                    <span class="tit">API</span>
                    <span class="cont">{{ router.name }}</span>
                </p>
                <p>
                    <span class="tit"></span>
                    <span class="cont">{{ router.description }}</span>
                </p>
            </div>
            <h3></h3>
            <div>
                <p>
                    <span class="tit"></span>
                    <span class="cont">{{ router.request_method }}</span>
                </p>
                <p>
                    <span class="tit"></span>
                    <span class="cont">{{ router.project_path + router.request_path }}</span>
                </p>
                <p>
                    <span class="tit"></span>
                    <span class="cont">{{ router.enable_cors ? '':'' }}</span>
                </p>
                <p>
                    <span class="tit"></span>
                    <el-table size="mini" :data="router.request_params" style="margin-left:40px;"
                              header-cell-style="background:#fafafa">
                        <el-table-column label="" prop="name"></el-table-column>
                        <el-table-column label="" prop="position"></el-table-column>
                        <el-table-column label="">
                            <template slot-scope="scope">{{ scope.row.required ? '' : '' }}</template>
                        </el-table-column>
                        <el-table-column label="" prop="type"></el-table-column>
                        <el-table-column label="" prop="default_val"></el-table-column>
                        <el-table-column label="" prop="description"></el-table-column>
                    </el-table>
                </p>
            </div>
            <h3></h3>
            <div>
                <p>
                    <span class="tit"></span>
                    <span class="cont">{{ router.backend_method }}</span>
                </p>
                <p>
                    <span class="tit"></span>
                    <span class="cont">{{ router.backend_path }}</span>
                </p>
                <p>
                    <span class="tit"></span>
                    <el-table size="mini" :data="router.backend_params" style="margin-left:40px;"
                              header-cell-style="background:#fafafa">
                        <el-table-column label="" prop="request_param_name"></el-table-column>
                        <el-table-column label="" prop="request_param_position"></el-table-column>
                        <el-table-column label="" prop="name">
                        </el-table-column>
                        <el-table-column label="" prop="position">
                        </el-table-column>
                        <el-table-column label="">
                            <template slot-scope="scope">
                                <div>{{ router.request_params[scope.$index].type }}</div>
                            </template>
                        </el-table-column>
                        <el-table-column label="" prop="request_param_description"></el-table-column>
                    </el-table>
                </p>
                <p>
                    <span class="tit"></span>
                    <el-table size="mini" :data="router.constant_params" style="margin-left:40px;"
                              header-cell-style="background:#fafafa">
                        <el-table-column label="" prop="name"></el-table-column>
                        <el-table-column label="" prop="position"></el-table-column>
                        <el-table-column label="" prop="value"></el-table-column>
                        <el-table-column label="" prop="description"></el-table-column>
                    </el-table>
                </p>
            </div>
            <h3></h3>
            <div>
                <p>
                    <span class="tit"></span>
                    <span class="cont">{{ router.response_type }}</span>
                </p>
                <p>
                    <span class="tit" style="margin-left:28px;margin-bottom:12px;"></span>
                    <el-input type="textarea" v-model="router.response_success" disabled
                              style="margin-left:32px;width:80%;"></el-input>
                </p>
                <p>
                    <span class="tit" style="margin-left:28px;margin-bottom:12px;"></span>
                    <el-input type="textarea" v-model="router.response_failure" disabled
                              style="margin-left:32px;width:80%;"></el-input>
                </p>
                <p>
                    <span class="tit" style="margin-bottom:12px"></span>
                    <el-table size="mini" :data="router.response_codes" style="margin-left:40px;"
                              header-cell-style="background:#fafafa">
                        <el-table-column label="" prop="code"></el-table-column>
                        <el-table-column label="" prop="message"></el-table-column>
                        <el-table-column label="" prop="descriptio"></el-table-column>
                    </el-table>
                </p>
            </div>
        </div>
    </el-drawer>
</template>

<script>

    export default {
        props: {
            routerId: {
                type: Number,
                default: 0,
            },
            size: {
                type: String,
                default: "60%"
            },
            direction: {
                type: String,
                default: "rtl"
            },
            open: {
                type: Boolean,
                default: false,
            },
            withHeader: {
                type: Boolean,
                default: false,
            },
            title: {
                type: String,
                default: "",
            }
        },
        data() {
            return {
                router: {}
            }
        },
        computed: {
            open_: {
                get() {
                    return this.open;
                },
                set(val) {
                    this.$emit("changeDetailRouter", val);
                }
            }
        },
        watch: {
            open(value) {
                if (value) {
                    if (this.routerId) {
                        this.getRouter();
                    } else {
                        this.cancelDetail();
                    }
                }
            }
        },
        methods: {
            cancelDetail() {
                this.open_ = false;
            },
            getRouter() {
                if (this.routerId > 0) {
                    this.$oakRequest.get(this.$oakBackendApis.router.query(this.routerId)).then(res => {
                        if (res.status === 200) {
                            let router = res.data.router;
                            if (Object.keys(router).length > 0) {
                                this.router = router;
                            } else {
                                this.router = {};
                            }
                        }
                    });
                }
            },
        }
    }
</script>

